<template><div class="markdown-body"><h1></h1>
<p><code v-pre="">mu-select</code> </p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true"></a> </h2>
<p> <code v-pre="">select</code>  <code v-pre="">mu-select</code>  <code v-pre="">mu-option</code> </p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-row gutter&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-select label=\&quot;Normal\&quot; v-model=\&quot;normal.value1\&quot; full-width&gt;\n        &lt;mu-option v-for=\&quot;option,index in options\&quot; :key=\&quot;option\&quot; :label=\&quot;option\&quot; :value=\&quot;option\&quot;&gt;&lt;/mu-option&gt;\n      &lt;/mu-select&gt;\n    &lt;/mu-col&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-select label=\&quot;Disabled Options\&quot; v-model=\&quot;normal.value3\&quot; full-width&gt;\n        &lt;mu-option v-for=\&quot;option,index in options\&quot; :disabled=\&quot;index % 2 !== 0\&quot; :key=\&quot;option\&quot; :label=\&quot;option\&quot; :value=\&quot;option\&quot;&gt;&lt;/mu-option&gt;\n      &lt;/mu-select&gt;\n    &lt;/mu-col&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-select label=\&quot;Error\&quot; error-text=\&quot;Please select an option\&quot; v-model=\&quot;normal.value2\&quot; full-width&gt;\n        &lt;mu-option v-for=\&quot;option in options\&quot; :key=\&quot;option\&quot; :label=\&quot;option\&quot; :value=\&quot;option\&quot;&gt;&lt;/mu-option&gt;\n      &lt;/mu-select&gt;\n    &lt;/mu-col&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-select label=\&quot;Disabled\&quot; disabled v-model=\&quot;normal.value4\&quot; full-width&gt;\n        &lt;mu-option v-for=\&quot;option in options\&quot; :key=\&quot;option\&quot; :label=\&quot;option\&quot; :value=\&quot;option\&quot;&gt;&lt;/mu-option&gt;\n      &lt;/mu-select&gt;\n    &lt;/mu-col&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-select label=\&quot;Readonly\&quot; readonly v-model=\&quot;normal.value5\&quot; full-width&gt;\n        &lt;mu-option v-for=\&quot;option in options\&quot; :key=\&quot;option\&quot; :label=\&quot;option\&quot; :value=\&quot;option\&quot;&gt;&lt;/mu-option&gt;\n      &lt;/mu-select&gt;\n    &lt;/mu-col&gt;\n  &lt;/mu-row&gt;\n&lt;/mu-container&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      options: [\n        'Option 1', 'Option 2', 'Option 3', 'Option 4',\n        'Option 5', 'Option 6', 'Option 7', 'Option 8',\n        'Option 9', 'Option 10'\n      ],\n      normal: {\n        value1: '',\n        value2: '',\n        value3: '',\n        value4: 'Option 1',\n        value5: 'Option 2'\n      }\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-row gutter="">
    <mu-col span="12" lg="4" sm="6">
      <mu-select label="Normal" v-model="normal.value1" full-width="">
        <mu-option v-for="option,index in options" :key="option" :label="option" :value="option"></mu-option>
      </mu-select>
    </mu-col>
    <mu-col span="12" lg="4" sm="6">
      <mu-select label="Disabled Options" v-model="normal.value3" full-width="">
        <mu-option v-for="option,index in options" :disabled="index % 2 !== 0" :key="option" :label="option" :value="option"></mu-option>
      </mu-select>
    </mu-col>
    <mu-col span="12" lg="4" sm="6">
      <mu-select label="Error" error-text="Please select an option" v-model="normal.value2" full-width="">
        <mu-option v-for="option in options" :key="option" :label="option" :value="option"></mu-option>
      </mu-select>
    </mu-col>
    <mu-col span="12" lg="4" sm="6">
      <mu-select label="Disabled" disabled v-model="normal.value4" full-width="">
        <mu-option v-for="option in options" :key="option" :label="option" :value="option"></mu-option>
      </mu-select>
    </mu-col>
    <mu-col span="12" lg="4" sm="6">
      <mu-select label="Readonly" readonly v-model="normal.value5" full-width="">
        <mu-option v-for="option in options" :key="option" :label="option" :value="option"></mu-option>
      </mu-select>
    </mu-col>
  </mu-row>
</mu-container>

</div>
                    
